body,html {
  margin: 0;
  font-family: 'Manrope', sans-serif;
  width: 100%;
  overflow-x: hidden;
  height: 100%;
  overflow: hidden;
  display: flex;
  flex: auto 1;
}
#root {
  width: 100%;
  overflow-x: hidden;
  height: 100%;
  overflow: hidden;
  display: flex;
  flex: auto 1;
}
.App {
  width: 100%;
  overflow-x: hidden;
  height: 100%;
  overflow-y: scroll;
}
.content-wrapper {
  padding: 40px 160px;
}
.header {
  background: url(./static/stadium.png);
  background-size: cover;
  height: 100%;
  width: 100vw;
  overflow: hidden;
  background-position: center;
  background-attachment: fixed;
  background-repeat: no-repeat;
  position: relative;
}
.header img.hand {
  position: absolute;
  bottom: 0;
  left: calc((100vw - 85vh) / 2);
}

.header .title {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.header .title > * {
  /* background: #ffffff80; */
  padding: 4px 8px;
  /* backdrop-filter: blur(200px); */
  /* background: linear-gradient(to right, #eff1f3 4%, #e2e2e2 25%, #eff1f3 36%); */
}

.header .title h1 {
  color: #ff4a3a;
  margin: 0;
  font-size: 32px;
  font-weight: bolder;
}
.header .title span {
  font-size: 20px;
  font-weight: normal;
  color: #1e1e6390;
  display: block;
}
.header .title h2 {
  color: #1e1e63;
  font-weight: 900;
  margin: 0;
}

.header .title img {
  width: 64px;
}

@media(min-width: 700px) {
  .header img.hand{
    height: 100%;
  }
  .header .title {
    bottom: 50px;
    left: 50px;
  }
}
@media(max-width: 700px) {
  .header {
    background-size: contain;
    text-align: center;
  }
  .header img.hand {
    width: 100vw;
    left: 0vw
  }
  .header .title {
    top: 24px;
    left: 24px;
    right: 24px;
    align-items: center;
    justify-content: center;
  }
  .header .title h1 {
    font-size: 20px;
  }
  .header .title span {
    font-size: 14px;
  }
  .header .title h2 {
    font-size: 20px;
  }
  .header .title > * {
    background: transparent;
  }
}


.game {
  padding: 120px 0;
  background: linear-gradient(45deg, #010070, #00003f);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: left center;
  /* border-radius: 12px; */
  /* background-size: 50% auto; */
}

.game.feature {
  background: #fff;
}

.game h2, .game h3, .game h1 {
  color: #fff;
  text-align: center;
  padding: 0 32px;
  margin: 0;
}

.game h2 {
  margin-bottom: 16px;
}

.game h1 {
  margin-top: 48px;
}

.game.feature h2, .game.feature h3 {
  color: #000;
}
.game h3 {
  font-weight: 100;
}

.game .content-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 1200px;
  margin: auto;
}

.game .instruction_wrapper {
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin-bottom: -40px;
}

.game .instruction_wrapper:nth-child(2n) {
  flex-direction: row-reverse;
}

.game .instruction {
  padding: 24px;
  margin: 20px 0;
  width: 50%;
  font-size: 24px;
  font-weight: lighter;
}

.game .instruction {
  color: rgba(255,255,255,0.5);
  cursor: pointer;
}

.game .instruction b {
  font-size: 34px;
  color: #fff;
}

.game.feature .instruction b {
  color: #010070;
}

.game.feature .instruction {
  color: rgba(0,0,0,0.5);
}

.game img {
  max-height: 50vh;
  max-width: 100%;
  border-radius: 40px;
  border: 12px solid #000;
  box-sizing: border-box;
}

.game.feature img {
  width: 50%;
  border: none;
}

.game.feature h1, .game.feature h2, .game.feature h3{
  color: #010070;
  margin: 16px;
} 

.game.feature h1 {
  font-size: 50px;
  letter-spacing: 26px;
}

.game .banner {
  background: rgba(255,255,255,0.1);
  margin: 0 32px;
  border-radius: 16px;
  padding: 16px 24px;
}

.game.feature .banner {
  background: #01007005;
}

@media(max-width: 1024px) {
  .content-wrapper {
    padding: 60px;
  }
  .game .instruction b {
    font-size: 23px;
  }
  .game .instruction {
    font-size: 18px;
  }
  .game.feature img {
    width: 100%;
    border: none;
  }
}

@media(max-width: 720px) {
  .game .instruction_wrapper,.game .instruction_wrapper:nth-child(2n) {
    flex-direction: column;
    text-align: center;
    margin-bottom: 0;
  }
  .game .instruction_wrapper .instruction {
    width: 100%;
  }
  .slides .content-wrapper {
    padding: 0;
  }
  .slides .instructions{
    white-space: nowrap;
    overflow-x: scroll;
    overflow-y: hidden;
  }

  .slides .instructions::-webkit-scrollbar-thumb {
    color: lawngreen;
  }
  .slides .instructions .instruction_wrapper {
    display: inline-block;
    max-width: 60vw;
    white-space: initial;
    vertical-align: top;
    padding: 32px;
    padding-right: 0;
    box-sizing: border-box;
    text-align: center;
  }
  .slides .instructions .instruction_wrapper:nth-last-child(1) {
    margin-right: 32px;
  }
  .slides .instructions .instruction {
    display: inline-block;
    max-width: 100%;
    white-space: initial;
    vertical-align: top;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    padding-top: 32px;
  }
  .slides .instruction b {
    font-size: 20px;
  }
  .slides .instruction {
    font-size: 18px;
  }
}

.footer {
  background: #05042f;
  text-align: left;
  color: #ffffff70;
  padding: 32px 72px;
  display: flex;
  justify-content: space-evenly;
  align-items: flex-start;
}


.footer b {
  color: #fff;
}

.footer .links {
  margin-bottom: 8px;
}

.footer .links a {
  text-decoration: none;
  color: rgba(255, 255, 255, 0.7);
  display: block;
  text-align: left;
  transition: .5s ease;
  margin-right: 16px;
}

.footer .links a:hover {
  color: rgba(255, 255, 255, 1);
  text-decoration: underline;
}

.footer .links a b {
  margin-bottom: 16px;
  display: block;
  font-size: 20px
}
.footer .links a:hover b{
  text-decoration: none !important;
}

.footer .sse {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.footer .wrapper {
  display: flex;
  align-self: stretch;
  width: 90%;
  max-width: 87.5em;
}

.footer .wrapper > * {
  padding: 24px;
}

.footer img {
  height: 50px;
}


@media(max-width: 720px) {
  .footer {
    padding: 16px;
    padding-bottom: 120px;
  }
  .footer .wrapper {
    flex-direction: column;

  }
}

.download_fixed {
  position: fixed;
  right: 32px;
  bottom: 32px;
  z-index: +5;
  display: flex;
  flex-direction: column;
}
.download_fixed a {
  color: #fff;
  background: linear-gradient(to right, #ff4a3a 4%, #ff4a3a 25%, #f14131 36%);
  text-decoration: none;
  padding: 12px 32px;
  border-radius: 100px;
  display: inline-block;
  font-size: 16px;
  margin-top: 8px;
  font-size: 24px !important;
}
.download_fixed span {
  background: #fff;
  margin-top: 4px;
  padding: 4px 12px;
  border-radius: 12px;
  font-size: 12px !important;
  text-align: center;
  background: rgb(1 0 88)!important;
  color: #fff !important;
}

@media(max-width: 720px) {
  .download_fixed {
    position: fixed;
    right: 32px;
    left: 32px;
    bottom: 16px;
    z-index: +5;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .download_fixed a {
    font-size: 20px !important;
  }
}

.ytplayer {
  margin: auto;
}